import React from 'react';
import type { FC, ReactNode } from 'react';

import { AccordionComponent } from './AccordionComponent';
import { Item } from './Item';

export interface IAccordionProps {
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –°–ø–∏—Å–æ–∫ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–µ */
  list: Array<IAccordionListItem>;
  /** –ú–∞–ª–µ–Ω—å–∫–∏–π —Ä–∞–∑–º–µ—Ä –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–∞ */
  small?: boolean;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–∞ */
  onChange: (id: number | string, expanded: boolean) => void;
}

export interface IAccordionListItem {
  /** –£–Ω–∏–∫–∞–ª—å–Ω—ã–µ –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä —ç–ª–µ–º–µ–Ω—Ç–∞ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–∞ */
  id: number | string;
  /** –ó–∞–≥–æ–ª–æ–≤–æ–∫ —ç–ª–µ–º–µ–Ω—Ç–∞ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–∞ */
  title: string;
  /** –ö–æ–Ω—Ç–µ–Ω—Ç —ç–ª–µ–º–µ–Ω—Ç–∞ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–∞ */
  content: ReactNode;
  /** C–æ—Å—Ç–æ—è–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–∞ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–∞ (–æ—Ç–∫—Ä—ã—Ç/–∑–∞–∫—Ä—ã—Ç) */
  expanded?: boolean;
}

export const Accordion: FC<IAccordionProps> = ({
  className,
  dataTestId,
  list,
  small = false,
  onChange,
}) => (
  <AccordionComponent className={className} data-test-id={dataTestId}>
    {list.map((item) => (
      <Item key={item.id} small={small} onChange={onChange} {...item} />
    ))}
  </AccordionComponent>
);
