  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –ø–æ–ª—è –≤–≤–æ–¥–∞ */
  disabled?: boolean;
  /** –°—Ç–∞—Ç—É—Å –ø–æ–ª—è –≤–≤–æ–¥–∞ */
  status?: Status;
  /** –®–∏—Ä–∏–Ω–∞ –∏–Ω–ø—É—Ç–∞ */
