import React from 'react';
import type { FC, KeyboardEvent, MouseEvent, ReactElement } from 'react';

import { KEY_CODES } from '../common';

import { CheckboxComponent } from './CheckboxComponent';
import { Mark } from './Mark';
import { IndeterminateMark } from './IndeterminateMark';
import { Input } from './Input';
import { Label } from './Label';
import { TickIcon, IndeterminateMarkIcon } from './icons';
import { CheckboxSize } from './constants';

export interface ICheckboxProps {
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è. –°—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –∏–ª–∏ –Ω–∞–∂–∞—Ç–∏–∏ Enter/Space –ø—Ä–∏ –ø–µ—Ä–µ—Ö–æ–¥–µ –ø–æ —Ç–∞–±—É */
  onChange: (
    evt: MouseEvent<HTMLDivElement> | KeyboardEvent<HTMLDivElement>,
    checked: boolean,
    id?: string | number
  ) => void;
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞: –≤—ã–±—Ä–∞–Ω/–Ω–µ –≤—ã–±—Ä–∞–Ω */
  checked?: boolean;
  /** –£–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä —á–µ–∫–±–æ–∫—Å–∞ */
  id?: string | number;
  /** –†–∞–∑–º–µ—Ä —á–µ–∫–±–æ–∫—Å–∞ */
  size?: CheckboxSize;
  /** –ü–æ–¥–ø–∏—Å—å –∫ —á–µ–∫–±–æ–∫—Å—É */
  label?: string | ReactElement;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞ */
  disabled?: boolean;
  /** –ù–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞ */
  indeterminate?: boolean;
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ –æ—à–∏–±–∫–∏ —á–µ–∫–±–æ–∫—Å–∞ */
  error?: boolean;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
}

export const Checkbox: FC<ICheckboxProps> = ({
  onChange,
  checked = false,
  id,
  size = 'big',
  label,
  disabled,
  indeterminate,
  error,
  className,
  dataTestId,
}: ICheckboxProps) => {
  const handleClick = (event: MouseEvent<HTMLDivElement>) => {
    if (!disabled) {
      onChange && onChange(event, !checked, id);
    }
  };

  const handleKeyUp = (event: KeyboardEvent<HTMLDivElement>) => {
    if (!disabled && (event.keyCode === KEY_CODES.ENTER || event.keyCode === KEY_CODES.SPACEBAR)) {
      onChange && onChange(event, !checked, id);
    }
  };

  const renderLabel = () => {
    if (typeof label === 'string') {
      const LabelWrapper = Label(size);
      return <LabelWrapper disabled={disabled}>{label}</LabelWrapper>;
    }

    return label;
  };

  const withLabel = Boolean(label);

  return (
    <CheckboxComponent
      className={className}
      data-test-id={dataTestId}
      size={size}
      tabIndex={1}
      disabled={disabled}
      onKeyUp={handleKeyUp}
      onClick={handleClick}
    >
      <Input type="checkbox" readOnly checked={checked} disabled={disabled} />
      {indeterminate ? (
        <IndeterminateMark disabled={disabled} size={size} tabIndex={-1} withLabel={withLabel}>
          <IndeterminateMarkIcon />
        </IndeterminateMark>
      ) : (
        <Mark
          checked={checked}
          disabled={disabled}
          error={!checked && error}
          size={size}
          tabIndex={-1}
          withLabel={withLabel}
        >
          <TickIcon />
        </Mark>
      )}
      {withLabel && renderLabel()}
    </CheckboxComponent>
  );
};
