import React, { FC, MouseEvent, KeyboardEvent } from 'react';

import { KEY_CODES } from '../common';

import { Mark } from './Mark';
import { Input } from './Input';
import { Label } from './Label';
import { RadioButtonComponent } from './RadioButtonComponent';
import { RadioButtonSize } from './constants';

export interface IRadioButtonProps {
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è. –°—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –∏–ª–∏ –Ω–∞–∂–∞—Ç–∏–∏ Enter/Space –ø—Ä–∏ –ø–µ—Ä–µ—Ö–æ–¥–µ –ø–æ —Ç–∞–±—É */
  onChange: (event: MouseEvent<HTMLDivElement> | KeyboardEvent<HTMLDivElement>, value: any) => void;
  /** –ü–æ–¥–ø–∏—Å—å –∫ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–µ */
  label: string;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  value: any;
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏: –≤—ã–±—Ä–∞–Ω–∞/–Ω–µ –≤—ã–±—Ä–∞–Ω–∞ */
  checked: boolean;
  /** –†–∞–∑–º–µ—Ä —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  size?: RadioButtonSize;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  disabled?: boolean;
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ –æ—à–∏–±–∫–∏ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  error?: boolean;
  /** –ê—Ç—Ä–∏–±—É—Ç name —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  name?: string;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
}

export const RadioButton: FC<IRadioButtonProps> = ({
  label,
  name,
  value,
  checked,
  disabled,
  error,
  size = 'big',
  className,
  dataTestId,
  onChange,
}: IRadioButtonProps) => {
  const handleClick = (event: MouseEvent<HTMLDivElement>) => {
    if (!disabled) {
      onChange && onChange(event, value);
    }
  };

  const handleKeyUp = (event: KeyboardEvent<HTMLDivElement>) => {
    if (!disabled && (event.keyCode === KEY_CODES.ENTER || event.keyCode === KEY_CODES.SPACEBAR)) {
      onChange && onChange(event, value);
    }
  };
  const LabelWrapper = Label(size);
  return (
    <RadioButtonComponent
      tabIndex={1}
      size={size}
      error={!checked && error}
      disabled={disabled}
      onKeyUp={handleKeyUp}
      onClick={handleClick}
      className={className}
      data-test-id={dataTestId}
    >
      <Input
        type="radio"
        readOnly
        name={name}
        checked={checked}
        value={value || ''}
        disabled={disabled}
      />
      <Mark tabIndex={-1} size={size} disabled={disabled} />
      <LabelWrapper disabled={disabled}>{label}</LabelWrapper>
    </RadioButtonComponent>
  );
};
