import React, { FC } from 'react';

import { Body2Long, Caption1 } from '../Typography';

import { BadgeComponent } from './BadgeComponent';
import type { Kind, Size } from './BadgeComponent';

export interface IBadgeProps {
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –¢–∏–ø –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  kind?: Kind;
  /** –†–∞–∑–º–µ—Ä –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  size?: Size;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ, –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º–æ–µ –≤ –±–µ–π–¥–∂–µ */
  value: number;
}

export const Badge: FC<IBadgeProps> = ({
  className,
  dataTestId,
  kind = 'neutral-light',
  size = 'big',
  value,
}) => {
  const Label = size === 'big' ? Body2Long : Caption1;
  return (
    <BadgeComponent className={className} data-test-id={dataTestId} kind={kind} size={size}>
      <Label>{value}</Label>
    </BadgeComponent>
  );
};
