import React, { FC, MouseEvent, KeyboardEvent } from 'react';

import { Body2Short, Body1Short } from '../Typography';
import { KEY_CODES } from '../common';

import { SwitchComponent } from './SwitchComponent';
import { SwitchInput } from './SwitchInput';
import { SwitchSlider } from './SwitchSlider';
import { SwitchWrapper } from './SwitchWrapper';
import type { LabelPosition, Size } from './SwitchSlider';

export interface ISwitchProps {
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ —Å–≤–∏—Ç—á–∞: –≤—ã—Å—Ç–∞–≤–ª–µ–Ω/–Ω–µ –≤—ã—Å—Ç–∞–≤–ª–µ–Ω */
  checked?: boolean;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è. –°—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –∏–ª–∏ –ø–µ—Ä–µ—Ö–æ–¥—É –ø–æ —Ç–∞–±—É */
  onChange: (
    event: MouseEvent<HTMLDivElement> | KeyboardEvent<HTMLDivElement>,
    checked: boolean
  ) => void;
  /** –ü–æ–¥–ø–∏—Å—å –∫ —Å–≤–∏—Ç—á—É */
  label?: string;
  /** –†–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –ø–æ–¥–ø–∏—Å–∏ */
  labelPosition?: LabelPosition;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ —Å–≤–∏—Ç—á–∞ */
  disabled?: boolean;
  /** –†–∞–∑–º–µ—Ä —Å–≤–∏—Ç—á–∞ */
  size?: Size;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
}

export const Switch: FC<ISwitchProps> = ({
  label,
  labelPosition = 'right',
  disabled = false,
  size = 'big',
  checked = false,
  className,
  dataTestId,
  onChange,
}) => {
  const handleClick = (event: MouseEvent<HTMLDivElement>) => {
    if (!disabled) {
      onChange && onChange(event, !checked);
    }
  };

  const handleKeyUp = (event: KeyboardEvent<HTMLDivElement>) => {
    if (!disabled && (event.keyCode === KEY_CODES.ENTER || event.keyCode === KEY_CODES.SPACEBAR)) {
      onChange && onChange(event, !checked);
    }
  };

  // To prevent focus event, firing on click
  const handleMouseDown = (event: MouseEvent<HTMLDivElement>) => event.preventDefault();

  const SwitchLabel = size === 'small' ? Body2Short : Body1Short;

  return (
    <SwitchComponent
      className={className}
      data-test-id={dataTestId}
      onKeyUp={handleKeyUp}
      onClick={handleClick}
      onMouseDown={handleMouseDown}
      disabled={disabled}
      tabIndex={1}
    >
      {label && labelPosition === 'left' && <SwitchLabel>{label}</SwitchLabel>}
      <SwitchWrapper>
        <SwitchInput checked={checked} readOnly $size={size} type="checkbox" />
        <SwitchSlider
          checked={checked}
          disabled={disabled}
          labelPosition={labelPosition}
          size={size}
        />
      </SwitchWrapper>
      {label && labelPosition === 'right' && <SwitchLabel>{label}</SwitchLabel>}
    </SwitchComponent>
  );
};
