import React from 'react';

import { RadioButton } from '../RadioButton';

import { RadioGroupSize } from './constants';
import { RadioGroupWrapper } from './RadioGroupWrapper';
import { Label } from './Label';

export interface IRadioGroupProps {
  /** –ó–Ω–∞—á–µ–Ω–∏–µ –≥—Ä—É–ø–ø—ã —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫ */
  value: any;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è. –°—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –∏–ª–∏ –Ω–∞–∂–∞—Ç–∏–∏ Enter/Space –ø—Ä–∏ –ø–µ—Ä–µ—Ö–æ–¥–µ –ø–æ —Ç–∞–±—É */
  onChange: (value: { value: any; id?: string }) => void;
  /** –°–ø–∏—Å–æ–∫ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫ */
  list: IRadioGroupListItem[];
  /** –ó–∞–≥–æ–ª–æ–≤–æ–∫ –≥—Ä—É–ø–ø—ã —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫ */
  label?: string;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –≥—Ä—É–ø–ø—ã —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫ */
  disabled?: boolean;
  /** id –≥—Ä—É–ø–ø—ã —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫ */
  id?: string;
  /** –†–∞–∑–º–µ—Ä –≥—Ä—É–ø–ø—ã —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫ */
  size?: RadioGroupSize;
  /** –ê—Ç—Ä–∏–±—É—Ç name –≥—Ä—É–ø–ø—ã —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–æ–∫, –≤ —Ä–∞–º–∫–∞—Ö –æ–¥–Ω–æ–π –≥—Ä—É–ø–ø—ã –≤—Å–µ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ –∏–º–µ—é—Ç –æ–¥–∏–Ω–∞–∫–æ–≤—ã–π name */
  name?: string;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
}

export interface IRadioGroupListItem {
  /** –ü–æ–¥–ø–∏—Å—å –∫ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–µ */
  label: string;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  value: any;
  /** –†–∞–∑–º–µ—Ä —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  size?: RadioGroupSize;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  disabled?: boolean;
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ –æ—à–∏–±–∫–∏ —Ä–∞–¥–∏–æ–∫–Ω–æ–ø–∫–∏ */
  error?: boolean;
}

export const RadioGroup: React.FC<IRadioGroupProps> = ({
  id,
  label,
  value,
  size = 'big',
  name,
  list,
  disabled,
  onChange,
  className,
  dataTestId,
}) => {
  const handleChange = (value: any) => {
    onChange && onChange({ value, id });
  };
  const LabelWrapper = Label(size);

  return (
    <RadioGroupWrapper size={size} className={className} data-test-id={dataTestId}>
      {label && (
        <LabelWrapper size={size} disabled={disabled}>
          {label}
        </LabelWrapper>
      )}
      {list.map((item, i) => {
        return (
          <RadioButton
            key={i}
            size={item.size || size}
            checked={item.value === value}
            disabled={item.disabled || disabled}
            error={!(item.value === value) && item.error}
            label={item.label}
            value={item.value}
            name={name}
            onChange={(_, value) => handleChange(value)}
          />
        );
      })}
    </RadioGroupWrapper>
  );
};
