import React, { ReactNode, FC, MouseEvent, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';

import { ReactComponent as CloseOutline } from '../Icons/service/CloseOutline.svg';
import { KEY_CODES } from '../common';
import { Button } from '../Button';
import type { IButtonProps } from '../Button';

import { ModalSize, SIZES_WIDTH, SIZES_HEIGHT } from './constants';
import { ModalComponent } from './ModalComponent';
import { Header } from './Header';
import { Content } from './Content';
import { Footer } from './Footer';
import { IconClose } from './IconClose';
import { Overlay } from './Overlay';

export interface IModalProps {
  /** –°–æ–¥–µ—Ä–∂–∏–º–æ–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ */
  children: ReactNode;
  /** –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä, –≤ –∫–æ—Ç–æ—Ä–æ–º –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç —Ä–∞–∑–º–µ—â–µ–Ω–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ (BODY –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é) */
  container?: Element;
  /** –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
  header: string;
  /** –†–∞–∑–º–µ—Ä */
  size?: ModalSize;
  /** –°–≤–æ–π—Å—Ç–≤–æ, —É–∫–∞–∑—ã–≤–∞—é—â–µ–µ, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç—Å—è –ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ */
  opened?: boolean;
  /** –°–≤–æ–π—Å—Ç–≤–æ, —É–∫–∞–∑—ã–≤–∞—é—â–µ–µ, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç—Å—è –ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ Esc */
  shouldCloseOnEsc?: boolean;
  /** –°–≤–æ–π—Å—Ç–≤–æ, —É–∫–∞–∑—ã–≤–∞—é—â–µ–µ, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç—Å—è –ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ Overlay.
   *  –û–±—â–∏–π —Å–ª—É—á–∞–π –∫–ª–∏–∫–∞ –≤–Ω–µ Modal —Ä–µ–∞–ª–∏–∑—É–µ—Ç—Å—è –≤–Ω–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ - –≤–Ω—É—Ç—Ä–∏ —ç—Ç–æ —Å–¥–µ–ª–∞—Ç—å
   *  –≤ –æ–±—â–µ–º —Å–ª—É—á–∞–µ –Ω–µ–≤–æ–∑–º–æ–∂–Ω–æ, —Ç–∞–∫ –∫–∞–∫ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —ç–ª–µ–º–µ–Ω—Ç—ã,
   *  –Ω–∞—Ö–æ–¥—è—â–∏–µ—Å—è –≤–Ω–µ –µ–≥–æ –∏–µ—Ä–∞—Ä—Ö–∏–∏ (–Ω–∞–ø—Ä–∏–º–µ—Ä, –≤—ã–ø–∞–¥–∞—é—â–∏–µ –º–µ–Ω—é, DatePicker-–∫–∞–ª–µ–Ω–¥–∞—Ä—å –∏ —Ç.–ø.)
   */
  shouldCloseOnOverlayClick?: boolean;
  /** –ö–Ω–æ–ø–∫–∞ –æ—Å–Ω–æ–≤–Ω–∞—è */
  primaryButton?: IButtonProps;
  /** –ö–Ω–æ–ø–∫–∞ –≤—Ç–æ—Ä–∏—á–Ω–∞—è */
  secondaryButton?: IButtonProps;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –ö–æ–ª–ª–±—ç–∫ –∑–∞–∫—Ä—ã—Ç–∏—è */
  onRequestClose?: () => void;
}

export const Modal: FC<IModalProps> = ({
  children,
  header,
  size = 'big',
  container = document.body,
  opened = false,
  shouldCloseOnEsc,
  shouldCloseOnOverlayClick = true,
  primaryButton,
  secondaryButton,
  className,
  dataTestId,
  onRequestClose,
}: IModalProps) => {
  const overlayRef = useRef<HTMLDivElement>(null);

  const handleClose = (e: any) => {
    e.stopPropagation();
    onRequestClose && onRequestClose();
  };

  const handleEscKeyDown = (e: KeyboardEvent) => {
    if (opened && shouldCloseOnEsc && e.keyCode === KEY_CODES.ESCAPE) {
      handleClose(e);
    }
  };

  const handleOverlayClick = (e: MouseEvent) => {
    if (opened && shouldCloseOnOverlayClick && e.target === overlayRef.current) {
      handleClose(e);
    }
  };

  useEffect(() => {
    document.addEventListener('keydown', handleEscKeyDown);
    return () => {
      document.removeEventListener('keydown', handleEscKeyDown);
    };
  });

  const buttonCount = (primaryButton ? 1 : 0) + (secondaryButton ? 1 : 0);
  return opened
    ? ReactDOM.createPortal(
        <Overlay ref={overlayRef} opened={opened} onClick={handleOverlayClick}>
          <ModalComponent
            width={SIZES_WIDTH[size]}
            height={SIZES_HEIGHT[size]}
            buttonCount={buttonCount}
            className={className}
            data-test-id={dataTestId}
          >
            <Header>{header}</Header>
            <IconClose markerOffset={6} onClick={handleClose}>
              <CloseOutline width={24} height={24} />
            </IconClose>
            <Content>{children}</Content>
            {buttonCount ? (
              <Footer>
                {secondaryButton && (
                  <Button
                    {...secondaryButton}
                    size={secondaryButton.size || 'small'}
                    kind={secondaryButton.kind || 'secondary'}
                  />
                )}
                {primaryButton && (
                  <Button {...primaryButton} size={primaryButton.size || 'small'} />
                )}
              </Footer>
            ) : (
              ''
            )}
          </ModalComponent>
        </Overlay>,
        container
      )
    : null;
};
