import React, { FC, useState } from 'react';

import {
  UPLOAD_TEXT,
  GET_FILE_COUNT_EXCEED_ERROR,
  GET_FILE_TOTAL_SIZE_ERROR,
  DEFAULT_MAX_TOTAL_SIZE,
  UPLOAD_TEXT_MOBILE,
} from './constants';
import {
  Dropzone,
  DropzoneFile,
  Container,
  HeaderTitle,
  HeaderSubtitle,
  ErrorText,
  IDropzoneFile,
} from './components';
export { formatBytesToMegabytes } from './utils';
export { DEFAULT_MAX_TOTAL_SIZE } from './constants';

export type { IDropzoneFile } from './components';

export interface IFileUploaderProps {
  /** –°–ø–∏—Å–æ–∫ –∑–∞–≥—Ä—É–∂–µ–Ω–Ω—ã—Ö —Ñ–∞–π–ª–æ–≤ */
  files?: IDropzoneFile[];
  /** –ö–æ–ª–ª–±–µ–∫ –ø—Ä–∏ –ø–æ–ø—ã—Ç–∫–µ –∑–∞–≥—Ä—É–∑–∫–∏ —Ñ–∞–π–ª–∞ */
  onChange?: (files: IDropzoneFile[]) => void;
  /** –ö–æ–ª–ª–±–µ–∫ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ —Ñ–∞–π–ª–∞ */
  onRemoveClick?: (file: IDropzoneFile, index: number) => void;
  /** –¢–µ–∫—Å—Ç, –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç—Å—è –≤ –æ–±–ª–∞—Å—Ç–∏ –∑–∞–≥—Ä—É–∑–∫–∏ */
  uploadText?: string;
  /** –ú–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π —Ä–∞–∑–º–µ—Ä —Ñ–∞–π–ª–∞ (–≤ –±–∞–π—Ç–∞—Ö) */
  maxSize?: number;
  /** –ú–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π —Å—É–º–º–∞—Ä–Ω—ã–π —Ä–∞–∑–º–µ—Ä —Ñ–∞–π–ª–æ–≤ (–≤ –±–∞–π—Ç–∞—Ö) */
  maxTotalSize?: number;
  /** –ú–∏–Ω–∏–º–∞–ª—å–Ω—ã–π —Ä–∞–∑–º–µ—Ä —Ñ–∞–π–ª–∞ (–≤ –±–∞–π—Ç–∞—Ö) */
  minSize?: number;
  /** –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ñ–∞–π–ª–æ–≤ */
  maxFiles?: number;
  /** –†–∞–∑—Ä–µ—à–µ–Ω–Ω—ã–µ —Ç–∏–ø—ã —Ñ–∞–π–ª–æ–≤, –ø–æ–¥—Ä–æ–±–Ω–µ–µ: https://github.com/react-dropzone/attr-accept */
  accept?: string | string[];
  /** –ù–∞—Ö–æ–¥–∏—Ç—Å—è –ª–∏ –≤ –≤—ã–∫–ª—é—á–µ–Ω–Ω–æ–º —Å–æ—Å—Ç–æ—è–Ω–∏–∏ */
  disabled?: boolean;
  /** –ö–æ–ª–ª–±–µ–∫ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –æ—à–∏–±–∫–∏ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –∫–æ–ª-–≤–∞ —Ñ–∞–π–ª–æ–≤ –±–æ–ª—å—à–µ —Ä–∞–∑—Ä–µ—à–µ–Ω–Ω–æ–≥–æ */
  getFileCountLimitExceedError?: (exceededCount: number) => string;
  /** –ö–æ–ª–ª–±–µ–∫ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –æ—à–∏–±–∫–∏ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Ñ–∞–π–ª–æ–≤ —Å—É–º–º–∞—Ä–Ω–æ –ø–æ —Ä–∞–∑–º–µ—Ä—É –ø—Ä–µ–≤—ã—à–∞—é—â–∏–µ –¥–æ–ø—É—Å—Ç–∏–º–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ */
  getFileTotalSizeLimitExceedError?: (exceededCount: number) => string;
  /** –¢–µ–∫—Å—Ç –æ—à–∏–±–∫–∏ */
  error?: string;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –ó–∞–≥–æ–ª–æ–≤–æ–∫ –¥—Ä–æ–ø–∑–æ–Ω—ã */
  title?: string;
  /** –ü–æ–¥–∑–∞–≥–æ–ª–æ–≤–æ–∫ –¥—Ä–æ–ø–∑–æ–Ω—ã */
  subtitle?: string;
  /** –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä, –≤ –∫–æ—Ç–æ—Ä–æ–º –ø—Ä–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç–∏ –º–æ–∂–Ω–æ –æ—Ç—Ä–∏—Å–æ–≤–∞—Ç—å —Ç—É–ª—Ç–∏–ø —á–µ—Ä–µ–∑ ReactDOM.createPortal */
  tooltipContainer?: Element | null;
  /** –ú–æ–±–∏–ª—å–Ω–∞—è –≤–µ—Ä—Å–∏—è */
  mobile?: boolean;
}

export const FileUploader: FC<IFileUploaderProps> = ({
  className,
  dataTestId,
  accept,
  maxSize,
  maxTotalSize = DEFAULT_MAX_TOTAL_SIZE,
  minSize,
  getFileCountLimitExceedError = () => GET_FILE_COUNT_EXCEED_ERROR,
  getFileTotalSizeLimitExceedError = GET_FILE_TOTAL_SIZE_ERROR,
  files = [],
  maxFiles,
  disabled,
  onChange,
  error,
  onRemoveClick,
  title,
  subtitle,
  tooltipContainer,
  mobile,
  uploadText = mobile ? UPLOAD_TEXT_MOBILE : UPLOAD_TEXT,
}) => {
  const [errorText, setErrorText] = useState('');
  const handleRemoveClick = (file: IDropzoneFile, index: number) => () => {
    if (onRemoveClick) {
      onRemoveClick(file, index);
    }
    setErrorText('');
  };
  const handleFileCountLimitExceed = (count: number) => {
    setErrorText(getFileCountLimitExceedError(count));
  };
  const handleFileTotalSizeLimitExceed = (maxTotalSize: number) => {
    setErrorText(getFileTotalSizeLimitExceedError(maxTotalSize));
  };
  const handleChange = (files: IDropzoneFile[]) => {
    if (onChange) {
      onChange(files);
    }
    setErrorText('');
  };

  const properMaxSize = maxSize ? Math.min(maxSize, maxTotalSize) : maxTotalSize;

  const loadingFileIndex = files.findIndex((f: IDropzoneFile) => f.loading);
  return (
    <Container className={className} data-test-id={dataTestId} mobile={mobile}>
      {title && <HeaderTitle disabled={disabled}> {title} </HeaderTitle>}
      {title && subtitle && <HeaderSubtitle disabled={disabled}> {subtitle} </HeaderSubtitle>}
      <Dropzone
        onChange={handleChange}
        maxFiles={maxFiles}
        disabled={disabled}
        accept={accept}
        maxSize={properMaxSize}
        maxTotalSize={maxTotalSize}
        onFileCountLimitExceed={handleFileCountLimitExceed}
        onFileTotalSizeLimitExceed={handleFileTotalSizeLimitExceed}
        minSize={minSize}
        files={files}
        uploadText={uploadText}
        mobile={mobile}
      />
      {files.map((file, index) => (
        <DropzoneFile
          key={`${file.name}${file.data?.size || ''}`}
          onRemoveClick={handleRemoveClick(file, index)}
          data={file}
          waitingLoad={loadingFileIndex > -1 && loadingFileIndex < index}
          tooltipContainer={tooltipContainer}
        />
      ))}
      {(errorText || error) && <ErrorText>{errorText || error}</ErrorText>}
    </Container>
  );
};
