import React, { useState, Dispatch, SetStateAction, ReactNode, FC } from 'react';

import { createTooltipHOC } from '../../TooltipHOC';
import { Body2Short } from '../../Typography';
import { IInputDefaultProps, useTooltipHook } from '../common';
import type { Type } from '../common/types';
import { DEFAULT_WIDTH } from '../constants';

import { InputSkeleton } from './InputSkeleton';
import { Label, Wrapper, Separator, StyledAdditionalText } from './StyledComponents';

export interface IInputProps extends IInputDefaultProps {
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∫–ª–∏–∫ */
  onClick?: (event: any) => void | undefined;
  /** –∏–∫–æ–Ω–∫–∏ –∏–∑ Figma , –∫–æ—Ç–æ—Ä—ã–µ –ø—Ä–æ–∫–∏–¥—ã–≤–∞—é—Ç—Å—è –≤ –∏–Ω–ø—É—Ç */
  iconServices?: ReactNode;
  /** –û—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø–æ–¥—Å–∫–∞–∑–∫–∏ –ø—Ä–∏ –ø–µ—Ä–µ–ø–æ–ª–Ω–µ–Ω–∏–∏ –∏–Ω–ø—É—Ç–∞. –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é false */
  withTooltip?: boolean;
  /** –¢–∏–ø –∏–Ω–ø—É—Ç–∞ */
  type?: Type;
  /** –ê–∫—Ç–∏–≤–Ω–∞ –ª–∏ –∏–∫–æ–Ω–∫–∞ –≤ –∏–Ω–ø—É—Ç–µ */
  iconIsActive?: boolean;
  /** –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è active icon */
  setIconIsActive?: Dispatch<SetStateAction<boolean>>;
  /** –ò—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —á–∏—Å–ª–æ–≤–æ–≥–æ –∏–Ω–ø—É—Ç–∞ */
  number?: boolean;
  /** –ò—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ Input Range */
  range?: boolean;
  /** –ê–∫—Ç–∏–≤–µ–Ω –ª–∏ –∏–Ω—Ñ–æ—Ä–º–µ—Ä –≤ –∏–Ω–ø—É—Ç–µ */
  activeInformer?: boolean;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ –∏–Ω–ø—É—Ç–∞ */
  value?: any;
  /** –§–ª–∞–≥ –≤–≤–æ–¥–∞ –¥—Ä–æ–±–Ω–æ–π —á–∞—Å—Ç–∏ */
  withCoins?: boolean;
  /** –î–µ—Ñ–æ–ª—Ç–Ω–æ–µ value */
  defaultValue?: any;
}

export const Input: FC<IInputProps> = ({
  disabled,
  label,
  additionalText,
  status = 'default',
  className,
  dataTestId,
  withTooltip = false,
  value = '',
  width = DEFAULT_WIDTH,
  refInput,
  tooltipContainer,
  ...props
}) => {
  const [focused, setFocused] = useState(false);
  const { tooltip, ref } = useTooltipHook(refInput, withTooltip, value);

  const Input = tooltip && !focused ? createTooltipHOC(InputSkeleton) : InputSkeleton;
  const inputElement = (
    <Input
      tooltip={value}
      container={tooltipContainer}
      display="block"
      disabled={disabled}
      focused={focused}
      setFocused={setFocused}
      status={status}
      value={value}
      ref={ref}
      width={width}
      {...props}
    />
  );

  return (
    <Wrapper
      disabled={disabled}
      focused={focused}
      className={className}
      data-test-id={dataTestId}
      width={width}
    >
      <Label>
        {label && (
          <>
            <Body2Short>{label}</Body2Short>
            <Separator />
          </>
        )}
        {inputElement}
      </Label>
      {additionalText && (
        <StyledAdditionalText disabled={disabled} status={status} focused={focused}>
          <Body2Short>{additionalText}</Body2Short>
        </StyledAdditionalText>
      )}
    </Wrapper>
  );
};
