import React, { FC, ReactNode } from 'react';

import { ReactComponent as PersonOutline } from '../Icons/system/PersonOutline.svg';

import type { Size, Kind } from './Wrapper';
import { Wrapper } from './Wrapper';
import { useLoaded } from './useLoaded';

export type IAvatarProps = {
  /** src –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è img —ç–ª–µ–º–µ–Ω—Ç–∞. –¢–∞–∫–∂–µ –≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –º–æ–∂–Ω–æ –ø–µ—Ä–µ–¥–∞—Ç—å icon –∏–ª–∏ text —ç–ª–µ–º–µ–Ω—Ç—ã –≤ –∫–∞—á–µ—Å—Ç–≤–µ children,
   * children –±—É–¥—É—Ç –æ—Ç—Ä–∏—Å–æ–≤–∞–Ω—ã, –µ—Å–ª–∏ src –Ω–µ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω –∏–ª–∏ –∑–∞–≥—Ä—É–∑–∫–∞ img –∑–∞–≤–µ—Ä—à–∏–ª–∞—Å—å –æ—à–∏–±–∫–æ–π */
  src?: string;
  /** srcSet –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è img —ç–ª–µ–º–µ–Ω—Ç–∞ */
  srcSet?: string;
  /** sizes –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è img —ç–ª–µ–º–µ–Ω—Ç–∞ */
  sizes?: string;
  /** alt –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è img —ç–ª–µ–º–µ–Ω—Ç–∞ */
  alt?: string;
  /** –¢–∏–ø –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  kind?: Kind;
  /** –†–∞–∑–º–µ—Ä –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  size?: Size;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
};

export const Avatar: FC<IAvatarProps> = ({
  className,
  dataTestId,
  kind = 'white',
  size = 56,
  src,
  srcSet,
  sizes,
  alt,
  children,
}) => {
  const hasImg = src || srcSet;
  const loaded = useLoaded({ src, srcSet });
  const hasImgLoaded = hasImg && loaded === 'loaded';

  const getInitials = (alt: string) => {
    const words = alt.split(' ');
    return words.length > 1 ? words[0][0] + words[1][0] : words[0][0];
  };
  const controlStringLength = (str: string) => {
    return str.length > 2 ? str.substr(0, 2) : str;
  };

  const getContent = () => {
    let content: ReactNode = null;
    if (hasImgLoaded) {
      content = <img alt={alt} src={src} srcSet={srcSet} sizes={sizes} />;
    } else if (children) {
      content = typeof children === 'string' ? controlStringLength(children) : children;
    } else if (hasImg && alt) {
      content = getInitials(alt);
    } else {
      content = <PersonOutline />;
    }
    return content;
  };

  const AvatarWrapper = Wrapper(size);

  return (
    <>
      <AvatarWrapper className={className} data-test-id={dataTestId} size={size} kind={kind}>
        {getContent()}
      </AvatarWrapper>
    </>
  );
};
