import React, { ReactNode, ElementType, FC } from 'react';

import { LinkComponent } from './LinkComponent';
import { Caption13 } from './Caption13';
import { Caption15 } from './Caption15';
import { IconContainer } from './IconContainer';
import { SIZES } from './constants';
import type { LinkKind, LinkSize, LinkIconPosition, LinkTarget } from './constants';
import type { BasicLinkProps } from './LinkComponent';

export interface ILinkProps extends BasicLinkProps {
  /** –¢–µ–∫—Å—Ç —Å—Å—ã–ª–∫–∏ */
  children: ReactNode;
  /** –ì–∏–ø–µ—Ä—Å—Å—ã–ª–∫–∞, –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è–µ—Ç URL –∏–ª–∏ —è–∫–æ—Ä—å (#ID —ç–ª–µ–º–µ–Ω—Ç–∞ –Ω–∞ —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ) */
  href: string;
  /** –ò–∫–æ–Ω–∫–∞, –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º–∞—è –≤–Ω—É—Ç—Ä–∏ —Å—Å—ã–ª–∫–∏ —Å–ª–µ–≤–∞ –∏–ª–∏ —Å–ø—Ä–∞–≤–∞ */
  icon?: ElementType;
  /** –í–∏–¥ —Å—Å—ã–ª–∫–∏ */
  kind?: LinkKind;
  /** –†–∞–∑–º–µ—Ä —Å—Å—ã–ª–∫–∏ */
  size?: LinkSize;
  /** –†–∞–∑–º–µ—â–µ–Ω–∏–µ –∏–∫–æ–Ω–∫–∏ —Å–ª–µ–≤–∞ –∏–ª–∏ —Å–ø—Ä–∞–≤–∞ —Ç–µ–∫—Å—Ç–∞ —Å—Å—ã–ª–∫–∏ */
  iconPosition?: LinkIconPosition;
  /* –ì–¥–µ –æ—Ç–æ–±—Ä–∞–∑–∏—Ç—å –ø–µ—Ä–µ—Ö–æ–¥ URL */
  target?: LinkTarget;
  /** –ò–Ω–≤–µ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å —Ü–≤–µ—Ç —Å—Å—ã–ª–∫–∏ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –Ω–∞ —Ç—ë–º–Ω—ã—Ö —Ñ–æ–Ω–∞—Ö */
  inverse?: boolean;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ —Å—Å—ã–ª–∫–∏ */
  disabled?: boolean;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
}

export const Link: FC<ILinkProps> = ({
  children,
  href,
  icon: Icon,
  kind = 'primary',
  size = 'big',
  target,
  iconPosition = Icon ? 'right' : undefined,
  inverse = false,
  disabled = false,
  className,
  dataTestId,
  ...props
}: ILinkProps) => {
  const Caption = size === 'big' ? Caption15 : Caption13;
  const iconSize = SIZES[size];
  return (
    <LinkComponent
      {...props}
      href={href}
      target={target}
      inverse={inverse}
      className={className}
      data-test-id={dataTestId}
      kind={kind}
      size={size}
      iconPosition={Icon ? iconPosition : undefined}
      disabled={disabled}
    >
      {Icon && iconPosition === 'left' && (
        <IconContainer size={size}>{<Icon width={iconSize} height={iconSize} />}</IconContainer>
      )}
      <Caption>{children}</Caption>
      {Icon && iconPosition === 'right' && (
        <IconContainer size={size}>{<Icon width={iconSize} height={iconSize} />}</IconContainer>
      )}
    </LinkComponent>
  );
};
