import React from 'react';
import type { FC } from 'react';

import { Caption1 } from '../Typography';

import { Bar } from './Bar';
import { Info } from './Info';
import { Progress } from './Progress';
import { ProgressBarComponent } from './ProgressBarComponent';
import type { Kind } from './ProgressBarComponent';

export interface IProgressBarProps {
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –§–ª–∞–≥ –æ—à–∏–±–∫–∏ */
  error?: boolean;
  /** –í–∏–¥ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  kind?: Kind;
  /** –ù–∞–¥–ø–∏—Å—Ç—å –Ω–∞–¥ –ø–æ–ª–æ—Å–æ–π –ø—Ä–æ–≥—Ä–µ—Å—Å–∞ */
  label?: string;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ –ø—Ä–æ–≥—Ä–µ—Å—Å–∞ –æ—Ç 1 –¥–æ 100 */
  value: number;
}

export const ProgressBar: FC<IProgressBarProps> = ({
  className,
  dataTestId,
  error = false,
  kind = 'page',
  label,
  value,
}) => (
  <ProgressBarComponent className={className} data-test-id={dataTestId} kind={kind}>
    {kind === 'page' && (
      <Info error={error}>
        <Caption1>{label}</Caption1>
        <Caption1>{`${value}%`}</Caption1>
      </Info>
    )}
    <Bar>
      <Progress error={error} value={value} />
    </Bar>
  </ProgressBarComponent>
);
