import React, { useState, FC, useRef, FocusEvent, useCallback } from 'react';

import type { Size } from '../Inputs/common/types';
import { getIconSize } from '../Inputs/common/utils';
import { InputMask } from '../Inputs/Mask';
import { ReactComponent as TimeOutline } from '../Icons/system/TimeOutline.svg';

import { ADDITIONAL_MESSAGE, DEFAULT_WIDTH, MASK } from './components/constants';
import { IconWrapper, Wrapper } from './StyledComponents';
import {
  timeValid,
  parseValue,
  formatValue,
  validateUserTimeRange,
  activeTimeParse,
} from './utils';
import { DropdownMenu } from './components/DropdownMenu';

export type TTime = {
  hours: string;
  minutes: string;
};

export interface ITimePickerProps {
  /** –ó–Ω–∞—á–µ–Ω–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  value: TTime;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è */
  onChange: (value: TTime) => void;
  /** –õ—ç–π–±–ª –∫ –∏–Ω–ø—É—Ç—É */
  label?: string;
  /** –†–∞–∑–º–µ—Ä –∏–Ω–ø—É—Ç–∞ */
  size?: Size;
  /** –§–ª–∞–≥ –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ */
  disabled?: boolean;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ —Å–æ–±—ã—Ç–∏–µ —Ñ–æ–∫—É—Å–∞ –∏–Ω–ø—É—Ç–∞ */
  onFocus?: (event: FocusEvent<HTMLInputElement>) => void;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ —Å–æ–±—ã—Ç–∏–µ –ø–æ—Ç–µ—Ä–∏ —Ñ–æ–∫—É—Å–∞ –∏–Ω–ø—É—Ç–∞ */
  onBlur?: (event: FocusEvent<HTMLInputElement>) => void;
  /** –ú–∏–Ω–∏–º–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∏–Ω—Ç–µ—Ä–≤–∞–ª–∞ —Å–µ–ª–µ–∫—Ç–∞ */
  selectRangeStart?: TTime;
  /** –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∏–Ω—Ç–µ—Ä–≤–∞–ª–∞ —Å–µ–ª–µ–∫—Ç–∞ */
  selectRangeEnd?: TTime;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** –£–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä */
  id?: string;
  /** –®–∏—Ä–∏–Ω–∞ –∏–Ω–ø—É—Ç–∞ */
  width?: string | number;
  /** –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π —Ç–µ–∫—Å—Ç –ø–æ–¥ –∏–Ω–ø—É—Ç–æ–º */
  additionalText?: string;
}

export const TimePicker: FC<ITimePickerProps> = ({
  value = { hours: '', minutes: '' },
  onChange,
  selectRangeStart,
  selectRangeEnd,
  dataTestId,
  className,
  size = 'big',
  disabled,
  additionalText = ADDITIONAL_MESSAGE,
  onBlur,
  onFocus,
  width = DEFAULT_WIDTH,
  id,
  ...props
}) => {
  const inputRef = useRef<any>(null);
  const [error, setError] = useState(false);
  const [menuOpen, setMenuOpen] = useState<boolean>(false);

  const valueInput = parseValue(value);

  const handleChange = (value: { value: string; masked: string }) => {
    setMenuOpen(false);
    onChange && onChange(formatValue(value));
  };

  const handleFocus = (event: FocusEvent<HTMLInputElement>) => {
    onFocus && onFocus(event);
    setError(false);
  };

  const handleBlur = (event: FocusEvent<HTMLInputElement>) => {
    setError(timeValid(value, valueInput));
    onBlur && onBlur(event);
    setMenuOpen(false);
  };

  const handleComplete = useCallback((value: string) => {
    onTimeSelect(value);
  }, []);

  const onTimeSelect = (time: string) => {
    const value = time.replace(/\D+/g, '');

    onChange &&
      onChange({
        hours: `${value[0]}${value[1]}`,
        minutes: `${value[2]}${value[3]}`,
      });
    setMenuOpen(false);
  };

  const handleClickIcon = () => {
    if (menuOpen) {
      setMenuOpen(false);
    } else {
      setMenuOpen(true);
      inputRef.current.focus();
    }
  };

  const { rangeStart, rangeEnd } = validateUserTimeRange(selectRangeStart, selectRangeEnd);
  const iconSize = getIconSize(size);

  const icon = (
    <IconWrapper disabled={disabled} active={menuOpen} onMouseDown={(e) => e.preventDefault()}>
      <TimeOutline
        width={iconSize}
        height={iconSize}
        onClick={disabled ? undefined : handleClickIcon}
      />
    </IconWrapper>
  );
  return (
    <Wrapper dataTestId={dataTestId} className={className} width={width}>
      <InputMask
        icon={icon}
        id={id}
        mask={MASK}
        onFocus={handleFocus}
        disabled={disabled}
        refInput={inputRef}
        onBlur={handleBlur}
        status={error ? 'error' : 'default'}
        additionalText={error ? additionalText : ''}
        onComplete={handleComplete}
        size={size}
        value={valueInput}
        onChange={handleChange}
        alwaysShowMask
        width={width}
        {...props}
      />
      <DropdownMenu
        size={size}
        open={menuOpen}
        onTimeSelect={onTimeSelect}
        activeTime={activeTimeParse(value)}
        rangeStart={rangeStart}
        rangeEnd={rangeEnd}
      />
    </Wrapper>
  );
};
