import React, { FC, MouseEvent, KeyboardEvent } from 'react';

import { Checkbox, ICheckboxProps } from '../Checkbox';

import { CheckboxGroupItem } from './CheckboxGroupItem';
import { CheckboxGroupWrapper } from './CheckboxGroupWrapper';
import { Label } from './Label';

export interface ICheckboxGroupItem {
  /** –£–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä —á–µ–∫–±–æ–∫—Å–∞ */
  id: string | number;
  /** –ü–æ–¥–ø–∏—Å—å –∫ —á–µ–∫–±–æ–∫—Å—É */
  label: string;
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞: –≤—ã–±—Ä–∞–Ω/–Ω–µ –≤—ã–±—Ä–∞–Ω */
  checked?: boolean;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞ */
  disabled?: boolean;
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ –æ—à–∏–±–∫–∏ —á–µ–∫–±–æ–∫—Å–∞ */
  error?: boolean;
}

export interface ICheckboxGroupProps {
  /** –°–ø–∏—Å–æ–∫ —á–µ–∫–±–æ–∫—Å–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –≤–æ–π–¥—É—Ç –≤ –≥—Ä—É–ø–ø—É */
  list: ICheckboxGroupItem[];
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è. –°—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –∏–ª–∏ –Ω–∞–∂–∞—Ç–∏–∏ Enter/Space –ø—Ä–∏ –ø–µ—Ä–µ—Ö–æ–¥–µ –ø–æ —Ç–∞–±—É */
  onChange: (
    evt: MouseEvent<HTMLDivElement> | KeyboardEvent<HTMLDivElement>,
    list: ICheckboxGroupItem[]
  ) => void;
  /** –ù–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã —á–µ–∫–±–æ–∫—Å–æ–≤ */
  label?: string;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –≥—Ä—É–ø–ø—ã —á–µ–∫–±–æ—Å–∫—Å–æ–≤ */
  disabled?: boolean;
  /** –†–∞–∑–º–µ—Ä –≥—Ä—É–ø–ø—ã —á–µ–∫–±–æ–∫—Å–æ–≤ */
  size?: ICheckboxProps['size'];
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
}

export const CheckboxGroup: FC<ICheckboxGroupProps> = ({
  list,
  onChange,
  label,
  disabled,
  size = 'big',
  className,
  dataTestId,
}: ICheckboxGroupProps) => {
  const handleChange = (
    evt: MouseEvent<HTMLDivElement> | KeyboardEvent<HTMLDivElement>,
    item: ICheckboxGroupItem
  ) => {
    if (!disabled) {
      const newItem = { ...item, checked: !item.checked };
      const newList = list.map((listItem: ICheckboxGroupItem) =>
        listItem.id === item.id ? newItem : listItem
      );

      onChange && onChange(evt, newList);
    }
  };

  const CheckboxGroupLabel = Label(size);

  return (
    <CheckboxGroupWrapper disabled={disabled} className={className} data-test-id={dataTestId}>
      {label && (
        <CheckboxGroupLabel disabled={disabled} size={size}>
          {label}
        </CheckboxGroupLabel>
      )}
      {list.map((item: ICheckboxGroupItem, i: number) => (
        <CheckboxGroupItem size={size} key={i}>
          <Checkbox
            size={size}
            onChange={(evt: MouseEvent<HTMLDivElement> | KeyboardEvent<HTMLDivElement>) =>
              handleChange(evt, item)
            }
            disabled={disabled || item.disabled}
            checked={!!item.checked}
            label={item.label}
            error={item.error}
          />
        </CheckboxGroupItem>
      ))}
    </CheckboxGroupWrapper>
  );
};
