  /** –û–ø—Ä–µ–¥–µ–ª—è–µ—Ç —Ä–∞—Å–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –ø—Ä–æ—Å—Ç—Ä–∞–Ω—Å—Ç–≤–∞ —É —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –≤–¥–æ–ª—å –≥–ª–∞–≤–Ω–æ–π –æ—Å–∏ flex –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
  justifyContent?: JustifyContent;
  /** –í—ã—Ä–∞–≤–Ω–∏–≤–∞–µ—Ç —ç–ª–µ–º–µ–Ω—Ç—ã —Ç–µ–∫—É—â–µ–π flex-–ª–∏–Ω–∏–∏ –∞–Ω–∞–ª–æ–≥–∏—á–Ω–æ justifyContent, –Ω–æ –≤ –ø–µ—Ä–ø–µ–Ω–¥–∏–∫—É–ª—è—Ä–Ω–æ–º –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–∏ */
