import React, {
  useRef,
  useEffect,
  useState,
  FocusEvent,
  Dispatch,
  FC,
  ReactNode,
  MutableRefObject,
  useCallback,
  KeyboardEvent,
} from 'react';

import { StyledErrorSolid } from '../Text/StyledErrorSolid';
import { Body2Short } from '../../Typography';
import type { Status, Size } from '../common';
import { KEY_CODES } from '../../common';

import { DropdownIndicator } from './DropdownIndicator';
import { ICON_SIZE_DEFAULT, ICON_SIZE_MICRO } from './constants';
import { checkPhoneMask, checkMaskFilled } from './utils';
import { PlaceholderMask } from './PlaceholderMask';
import {
  formatValueInMaskValue,
  unFormatValue,
  formatValueOnComplete,
  getInputValue,
} from './MaskUtils';
import {
  Label,
  LabelText,
  StyledInput,
  AdditionalText,
  IconWrapper,
  InputWrapper,
  InputBox,
  PhoneCode,
} from './StyledComponents';

interface IInputMaskFieldProps {
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** –õ–µ–π–±–µ–ª –∏–Ω–ø—É—Ç–∞ */
  label?: string;
  /** –ü–æ–¥—Å–∫–∞–∑–∫–∞ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ */
  placeholder: string;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ  –∑–Ω–∞—á–µ–Ω–∏—è –∏–Ω–ø—É—Ç–∞ */
  onChange: any;
  /** –ú–∞—Å–∫–∞ –∏–Ω–ø—É—Ç–∞ */
  mask: string;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ –∏–Ω–ø—É—Ç–∞ */
  value: any;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Ñ–æ–∫—É—Å–∞ */
  onFocus?: (event: FocusEvent<HTMLInputElement>) => void;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ  –±–ª—é—Ä */
  onBlur?: (event: FocusEvent<HTMLInputElement>) => void;
  /** –û–±—Ä–∞–±–æ—Ç—á–∏ –¥–ª—è –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è —Ñ–ª–∞–≥–∞ focused */
  setFocused: Dispatch<React.SetStateAction<boolean>>;
  /** –§–ª–∞–≥  —Ñ–æ–∫—É—Å–µ –∏–Ω–ø—É—Ç –∏–ª–∏ –Ω–µ—Ç */
  focused: boolean;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –ø–æ–ª—è –≤–≤–æ–¥–∞ */
  disabled?: boolean;
  /** –°—Ç–∞—Ç—É—Å –ø–æ–ª—è –≤–≤–æ–¥–∞ */
  status?: Status;
  /** –®–∏—Ä–∏–Ω–∞ –∏–Ω–ø—É—Ç–∞ */
  width?: string | number;
  /** –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π —Ç–µ–∫—Å—Ç –∏–Ω–ø—É—Ç–∞ */
  additionalText?: string;
  /** –ò–∫–æ–Ω–∫–∞ –¥–ª—è –∏–Ω–ø—É—Ç–∞ */
  icon?: ReactNode;
  /** –†–∞–∑–º–µ—Ä –∏–Ω–ø—É—Ç–∞ */
  size?: Size;
  /** –û—Ç–æ–±—Ä–∞–∂–∞—Ç—å –ª–∏ –∏–∫–æ–Ω–∫—É –æ—à–∏–±–∫–∏ */
  errorIcon?: boolean;
  /** –í–Ω–µ—à–Ω–∏–π —Ä–µ—Ñ */
  refInput?: any;
  /** –ö–æ–ª–±–µ–∫ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç value –∫–æ–≥–¥–∞ –∑–∞–∫–æ–Ω—á–µ–Ω –≤–≤–æ–¥ –ø–æ –º–∞—Å–∫–µ */
  onComplete?: (value: any) => void;
  /** –ö–æ–ª–±–µ–∫ –¥–ª—è —Ñ–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏—è value */
  formatValue?: (value: Date | Date[] | string) => string;
  /** –ö–æ–ª–±–µ–∫ –¥–ª—è –¥–µ—Ñ–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏—è value */
  removeFormatValue?: (value: string, value2?: string) => Date | Array<Date | null> | string | null;
  /** –ü–æ–∫–∞–∑—ã–≤–∞—Ç—å –≤—Å–µ–≥–¥–∞ –º–∞—Å–∫—É */
  alwaysShowMask?: boolean;
  /** –£–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä */
  id?: string;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –Ω–∞–∂–∞—Ç–∏–µ –∫–ª–∞–≤–∏—à */
  onKeyDown?: (event: KeyboardEvent<HTMLInputElement>) => void;
  CountryIcon?: any;
  opened?: boolean;
  setOpened?: Dispatch<React.SetStateAction<boolean>>;
  phoneCountry?: boolean;
}

export const MaskField: FC<IInputMaskFieldProps> = ({
  className,
  label,
  placeholder,
  onChange,
  onFocus,
  onBlur,
  setFocused,
  alwaysShowMask,
  focused,
  disabled,
  status = 'default',
  additionalText,
  size = 'big',
  mask,
  icon,
  errorIcon,
  refInput,
  value = '',
  onComplete,
  formatValue,
  id,
  removeFormatValue,
  onKeyDown,
  CountryIcon,
  opened,
  setOpened,
  phoneCountry,
}) => {
  const [prevInput, setPrevInput] = useState({ value: '', cursorStart: 0 });
  const [errorActive, setErrorActive] = useState(true);
  const [keyDown, setKeyDown] = useState(false);

  const inputRef: MutableRefObject<HTMLInputElement | null> = useRef(null);

  const code = mask.slice(0, mask.indexOf('(') + 1);
  const maskPhone = checkPhoneMask(mask) ? mask.slice(mask.indexOf('(') + 1) : mask;

  const inputValue: any = getInputValue(value, maskPhone, focused, formatValue, alwaysShowMask);

  const handleComplete = useCallback(() => {
    onComplete && onComplete(formatValueOnComplete(inputValue, mask, removeFormatValue));
  }, [inputValue, mask, onComplete]);

  useEffect(() => {
    if (checkMaskFilled(mask, inputValue)) {
      handleComplete();
    }
    if (!refInput) {
      if (inputRef.current && prevInput.value !== inputValue) {
        const position = prevInput.cursorStart;
        inputRef.current.setSelectionRange(position, position);
      }
    } else {
      if (refInput.current && prevInput.value !== inputValue) {
        if (inputValue.length === mask.length && !keyDown) {
          refInput.current.setSelectionRange(inputValue.length, inputValue.length);
        } else {
          const position = prevInput.cursorStart;
          refInput.current.setSelectionRange(position, position);
        }
      }
    }
  }, [handleComplete, inputValue, prevInput.cursorStart, prevInput.value]);

  const handleKeyDown = (event: any) => {
    onKeyDown?.(event);
    setKeyDown(true);
    if (event.keyCode === KEY_CODES.ENTER) {
      inputRef.current?.blur();
      refInput?.current?.blur();
    }
  };

  const handleFocus = (event: FocusEvent<HTMLInputElement>) => {
    onFocus?.(event);
    setErrorActive(false);
    setFocused(true);
  };

  const handleBlur = (event: FocusEvent<HTMLInputElement>) => {
    onBlur?.(event);
    !opened && setFocused(false);
    setKeyDown(false);
    setErrorActive(true);
  };

  const handleClick = () => phoneCountry && setOpened?.(false);

  const handleChange = (event: any) => {
    const inputRef = event.target;
    const inputValue = inputRef.value;

    const cursorStart = formatValueInMaskValue(
      unFormatValue(inputValue.slice(0, inputRef.selectionStart), maskPhone),
      maskPhone
    ).length;

    setPrevInput({ value: inputValue, cursorStart });

    const newValue = unFormatValue(inputValue, maskPhone);

    if (value !== newValue) {
      onChange({
        value: newValue,
        masked: checkPhoneMask(mask)
          ? code + formatValueInMaskValue(newValue, maskPhone)
          : formatValueInMaskValue(newValue, maskPhone),
      });
    }
  };

  const iconSize = 'micro' === size ? ICON_SIZE_MICRO : ICON_SIZE_DEFAULT;
  const isPlaceholder = !focused && inputValue?.length === 0 ? placeholder : undefined;
  const showPhoneMask = checkPhoneMask(mask);
  const showPhoneCode = alwaysShowMask || inputValue.length > 0 || focused;

  return (
    <InputWrapper disabled={disabled} focused={focused}>
      <Label className={className}>
        {label && (
          <LabelText>
            <Body2Short>{label}</Body2Short>
          </LabelText>
        )}
        <InputBox focused={focused} disabled={disabled} size={size} status={status}>
          {phoneCountry && (
            <DropdownIndicator
              size={size}
              phoneCode={code}
              opened={opened}
              setOpened={setOpened}
              setFocused={setFocused}
              disabled={disabled}
            >
              <CountryIcon width={iconSize} height={iconSize} />
            </DropdownIndicator>
          )}

          {showPhoneMask ? (
            <PhoneCode disabled={disabled} size={size} onMouseDown={(e) => e.preventDefault()}>
              {showPhoneCode && code}
            </PhoneCode>
          ) : (
            <PlaceholderMask
              alwaysShowMask={alwaysShowMask}
              visibleValue={inputValue}
              mask={mask}
              size={size}
              focused={focused}
              disabled={disabled}
            />
          )}

          <StyledInput
            withPadding={showPhoneMask}
            $size={size}
            disabled={disabled}
            id={id ? id : 'input-mask'}
            focused={focused}
            placeholder={isPlaceholder}
            ref={refInput === undefined ? inputRef : refInput}
            onChange={handleChange}
            value={inputValue}
            onKeyDown={handleKeyDown}
            onFocus={handleFocus}
            onBlur={handleBlur}
            onClick={handleClick}
            autoComplete="off"
          />
          <IconWrapper>
            {icon && !errorIcon && icon}
            {status === 'error' && errorIcon && !disabled && (
              <StyledErrorSolid width={iconSize} $active={!errorActive} />
            )}
          </IconWrapper>
        </InputBox>
        {additionalText && !disabled && (
          <AdditionalText disabled={disabled} status={status} focused={focused}>
            <Body2Short>{additionalText}</Body2Short>
          </AdditionalText>
        )}
      </Label>
    </InputWrapper>
  );
};
