import { useCallback, useEffect, useState, useRef, MutableRefObject, RefObject } from 'react';

/**
 * –•—É–∫ –¥–ª—è —Ç—É–ª—Ç–∏–ø–∞. scrollWidth > offsetWidth
 * @param {MutableRefObject<HTMLInputElement> | MutableRefObject<undefined> | RefObject<HTMLInputElement>;} refInput - –í–Ω–µ—à–Ω–∏–π —Ä–µ—Ñ
 * @param {boolean} withTooltip - –ò–Ω–ø—É—Ç —Å  —Ç—É–ª—Ç–∏–ø–æ–º –∏–ª–∏ –±–µ–∑
 * @param {string}  value - Value –∏–∑ –∏–Ω–ø—É—Ç–∞
 * @return {boolean, MutableRefObject<HTMLInputElement> | null } - –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Ñ–ª–∞–≥ tooltip, –∏ —Ä–µ—Ñ –µ—Å–ª–∏ –≤–Ω–µ—à–Ω–µ–≥–æ —Ä–µ—Ñ–∞ –Ω–µ—Ç, —Ç–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–π
 */

export const useTooltipHook = (
  refInput:
    | MutableRefObject<HTMLInputElement>
    | MutableRefObject<null>
    | RefObject<HTMLInputElement>
    | undefined,
  withTooltip: boolean,
  value: string
): {
  tooltip: boolean;
  ref:
    | MutableRefObject<HTMLInputElement>
    | MutableRefObject<null>
    | RefObject<HTMLInputElement>
    | undefined;
} => {
  const [tooltip, setTooltip] = useState<boolean>(false);

  const _ref: MutableRefObject<HTMLInputElement> | MutableRefObject<null> = useRef(null);
  const ref = refInput ? refInput : _ref;

  const limitedSizeInput = useCallback(() => {
    if (!withTooltip) return;

    if (ref.current) {
      setTooltip(ref.current.scrollWidth > ref.current.offsetWidth);
    }
  }, [ref, setTooltip, withTooltip]);

  useEffect(() => {
    if (withTooltip) limitedSizeInput();

    return () => {
      return;
    };
  }, [value, limitedSizeInput, withTooltip]);

  return { tooltip, ref };
};
