import React, { FC } from 'react';

import { Page } from './Page';
import { PaginationComplexComponent } from './PaginationComplexComponent';
import { Range } from './Range';

const DEFAULT_RANGES = [20, 40, 60, 80];

export interface IPaginationComplexProps {
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –û—Ç–∫–ª—é—á–∏—Ç—å –≤—ã–±–æ—Ä –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ —á–µ—Ä–µ–∑ –≤—ã–ø–∞–¥–∞—é—â–µ–µ –º–µ–Ω—é */
  disableItemsPerPageSelect?: boolean;
  /** –û—Ç–∫–ª—é—á–∏—Ç—å –≤—ã–±–æ—Ä —Å—Ç—Ä–∞–Ω–∏—Ü—ã —á–µ—Ä–µ–∑ –≤—ã–ø–∞–¥–∞—é—â–µ–µ –º–µ–Ω—é */
  disablePageSelect?: boolean;
  /** –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ */
  itemsPerPage: number;
  /** –ú–æ–±–∏–ª—å–Ω—ã–π –≤–∏–¥ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  mobile?: boolean;
  /** –°—Ç—Ä–∞–Ω–∏—Ü–∞ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è */
  page: number;
  /** –í–∞—Ä–∏–∞–Ω—Ç—ã –≤—ã–±–æ—Ä–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ */
  ranges?: Array<number>;
  /** –ß–∏—Å–ª–æ –≤—Å–µ—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
  totalItems: number;
  /** –®–∏—Ä–∏–Ω–∞ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  width?: number | string;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ */
  onItemsPerPageChange?: (newItemsPerPage: number) => void;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã */
  onPageChange: (newPage: number) => void;
}

export const PaginationComplex: FC<IPaginationComplexProps> = ({
  className,
  dataTestId,
  disableItemsPerPageSelect = false,
  disablePageSelect = false,
  itemsPerPage,
  mobile = false,
  page,
  ranges = DEFAULT_RANGES,
  totalItems,
  width,
  onItemsPerPageChange,
  onPageChange,
}) => {
  const lastPage = Math.ceil(totalItems / itemsPerPage);

  return (
    <PaginationComplexComponent className={className} data-test-id={dataTestId} mobile={mobile}>
      <Range
        disableItemsPerPageSelect={disableItemsPerPageSelect}
        itemsPerPage={itemsPerPage}
        lastPage={lastPage}
        mobile={mobile}
        page={page}
        ranges={ranges}
        totalItems={totalItems}
        onChange={onItemsPerPageChange}
      />
      <Page
        disablePageSelect={disablePageSelect}
        lastPage={lastPage}
        mobile={mobile}
        page={page}
        onChange={onPageChange}
      />
    </PaginationComplexComponent>
  );
};
