import React from 'react';
import type { FC, ReactElement } from 'react';

import { Checkbox } from '../Checkbox';

import { ChildrenLayout } from './ChildrenLayout';
import { CustomLabel } from './CustomLabel';
import { Toggle } from './Toggle';
import { TreeBranchComponent } from './TreeBranchComponent';
import type { Size } from './TreeBranchComponent';

export interface ITreeBranchProps {
  /** –°–æ—Å—Ç–æ—è–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞: –≤—ã–±—Ä–∞–Ω/–Ω–µ –≤—ã–±—Ä–∞–Ω */
  checked?: boolean;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –ü–æ–∫–∞–∑—ã–≤–∞—Ç—å/—Å–∫—Ä—ã–≤–∞—Ç—å –¥–æ—á–µ—Ä–Ω–∏–∏ –≤–µ—Ç–∫–∏ */
  expanded?: boolean;
  /** –û—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –≤–µ—Ç–∫–∏ –∫–∞–∫ –∑–∞–≥–æ–ª–æ–≤–∫–∞ (–∂–∏—Ä–Ω—ã–π —Ç–µ–∫—Å—Ç) */
  header?: boolean;
  /** –°–∫—Ä—ã—Ç—å —á–µ–∫–±–æ–∫—Å –∏ –æ—Ç–æ–±—Ä–∞–∂–∞—Ç—å —Ç–æ–ª—å–∫–æ —Ç–µ–∫—Å—Ç (–∏ –∏–∫–æ–Ω–∫—É) */
  hideCheckbox?: boolean;
  /** –ò–∫–æ–Ω–∫–∞, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –æ—Ç–æ–±—Ä–∞–∂–∞—Ç—å—Å—è —Å–ª–µ–≤–∞ –æ—Ç —Ç–µ–∫—Å—Ç–∞ */
  icon?: ReactElement;
  /** –£–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –≤–µ—Ç–∫–∏ */
  id: string | number;
  /** –ù–µ–æ–ø—Ä–µ–¥–µ–ª—ë–Ω–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ —á–µ–∫–±–æ–∫—Å–∞ */
  indeterminate?: boolean;
  /** –¢–µ–∫—Å—Ç, –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –æ—Ç–æ–±—Ä–∞–∂–∞—Ç—å—Å—è –≤ –≤–µ—Ç–∫–µ */
  label: string;
  /** –†–∞–∑–º–µ—Ä –≤–µ—Ç–∫–∏ */
  size?: Size;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è —á–µ–∫–±–æ–∫—Å–∞. –°—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –∏–ª–∏ –Ω–∞–∂–∞—Ç–∏–∏ Enter/Space –ø—Ä–∏ –ø–µ—Ä–µ—Ö–æ–¥–µ –ø–æ —Ç–∞–±—É */
  onCheck?: (id: string | number, checked: boolean) => void;
  /** –ö–æ–ª–ª–±—ç–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è –ø–µ—Ä–µ–∫–ª—é—á–∞—Ç–µ–ª—è, –æ—Ç–≤–µ—á–∞—é—â–µ–≥–æ –∑–∞ –ø–æ–∫–∞–∑/—Å–∫—Ä—ã—Ç–∏–µ –¥–æ—á–µ—Ä–Ω–∏—Ö –≤–µ—Ç–æ–∫. –°—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –∏–ª–∏ –Ω–∞–∂–∞—Ç–∏–∏ Enter/Space –ø—Ä–∏ –ø–µ—Ä–µ—Ö–æ–¥–µ –ø–æ —Ç–∞–±—É */
  onExpand?: (id: string | number, expanded: boolean) => void;
}

export const TreeBranch: FC<ITreeBranchProps> = ({
  checked,
  children,
  className,
  dataTestId,
  expanded,
  header,
  hideCheckbox,
  icon,
  id,
  indeterminate,
  label,
  size = 'big',
  onCheck,
  onExpand,
}) => {
  const renderLabel = () => {
    if (!hideCheckbox && !header && !icon) return label;
    return (
      <CustomLabel header={header} icon={icon} size={size}>
        {label}
      </CustomLabel>
    );
  };

  return (
    <>
      <TreeBranchComponent className={className} data-test-id={dataTestId} size={size}>
        {children && (
          <Toggle expanded={expanded} size={size} onChange={() => onExpand?.(id, !expanded)} />
        )}
        {hideCheckbox ? (
          renderLabel()
        ) : (
          <Checkbox
            checked={checked}
            indeterminate={indeterminate}
            label={renderLabel()}
            size={size}
            onChange={(_, checked) => onCheck?.(id, checked)}
          />
        )}
      </TreeBranchComponent>
      {expanded && children && <ChildrenLayout size={size}>{children}</ChildrenLayout>}
    </>
  );
};
