import React, {
  useState,
  FC,
  useRef,
  RefObject,
  MutableRefObject,
  ChangeEvent,
  FocusEvent,
  KeyboardEvent,
} from 'react';

import type { Status, Size } from '../common';

import { defaultFilterOptions, getInputValue } from './utils';
import { DropDownMenu } from './DropDownMenu';
import { Input } from './Input';
import { Wrapper } from './styled-components';
import {
  CHARS_TO_SHOW_SUGGEST,
  DEFAULT_NOOP_MESSAGE,
  DEFAULT_LOAD_MESSAGE,
  DEFAULT_MENU_HEIGHT,
} from './constants';

export interface IInputSuggest {
  /** –®–∏—Ä–∏–Ω–∞ –∏–Ω–ø—É—Ç–∞ */
  width?: string | number;
  /** –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –ø–æ–ª—è –≤–≤–æ–¥–∞ */
  disabled?: boolean;
  /** –õ–µ–π–±–ª –∏–Ω–ø—É—Ç–∞ */
  label?: string;
  /** –ü–æ–¥—Å–∫–∞–∑–∫–∞ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ */
  placeholder?: string;
  /** –¢–µ–∫—Å—Ç –ø–æ–¥ –∏–Ω–ø—É—Ç–æ–º */
  additionalText?: string;
  /** –°—Ç–∞—Ç—É—Å */
  status?: Status;
  /** –ê—Ç—Ç—Ä–∏–±—É—Ç name */
  inputName?: string;
  /** –£–Ω–∏–∫–∞–ª—å–Ω—ã–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä */
  id?: string;
  /** –ò–º—è –∫–ª–∞—Å—Å–∞ –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π */
  className?: string;
  /** data-test-id –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */
  dataTestId?: string;
  /** –†–∞–∑–º–µ—Ä –∏–Ω–ø—É—Ç–∞ */
  size?: Size;
  /** Ref –∏–Ω–ø—É—Ç–∞ */
  refInput?:
    | MutableRefObject<HTMLInputElement>
    | MutableRefObject<null>
    | RefObject<HTMLInputElement>;
  /** –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä, –≤ –∫–æ—Ç–æ—Ä–æ–º –ø—Ä–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç–∏ –º–æ–∂–Ω–æ –æ—Ç—Ä–∏—Å–æ–≤–∞—Ç—å —Ç—É–ª—Ç–∏–ø —á–µ—Ä–µ–∑ ReactDOM.createPortal */
  tooltipContainer?: Element | null;
  /** –ú–∞—Å—Å–∏–≤ –æ–ø—Ü–∏–π */
  options: (string | IOptionSuggest<any>)[];
  /** –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–º–≤–æ–ª–æ–≤, –ø–æ—Å–ª–µ –∫–æ—Ç–æ—Ä–æ–≥–æ –±—É–¥—É—Ç –ø–æ–∫–∞–∑–∞–Ω—ã –ø–æ–¥—Å–∫–∞–∑–∫–∏ */
  charsToShowSuggest?: number;
  /** –¢–µ–∫—Å—Ç —Å–æ–æ–±—â–µ–Ω–∏—è –µ—Å–ª–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω –Ω–∏ –æ–¥–∏–Ω —ç–ª–µ–º–µ–Ω—Ç */
  noopMessage?: string;
  /** –ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö */
  load?: boolean;
  /** –¢–µ–∫—Å—Ç —Å–æ–æ–±—â–µ–Ω–∏—è  –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥–∞–Ω–Ω—ã—Ö */
  loadMessage?: string;
  /** –û—á–∏—â–∞–µ–º–æ–µ –ø–æ–ª–µ */
  clearable?: boolean;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ –±–ª—é—Ä */
  onBlur?: (event: FocusEvent<HTMLInputElement>, value: string) => void;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Ñ–æ–∫—É—Å–∞ */
  onFocus?: (event: FocusEvent<HTMLInputElement>) => void;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –Ω–∞–∂–∞—Ç–∏–µ –∫–ª–∞–≤–∏—à */
  onKeyDown?: (event: KeyboardEvent<HTMLInputElement>) => void;
  /** –§—É–Ω–∫—Ü–∏—è —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏ –æ–ø—Ü–∏–π */
  filterOptions?: (options: (string | IOptionSuggest<any>)[]) => (string | IOptionSuggest<any>)[];
  /** –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è –≤—ã—Å–æ—Ç–∞ –≤—ã–ø–∞–¥–∞—é—â–µ–≥–æ –º–µ–Ω—é */
  menuMaxHeight?: string | number;
  /** –í—ã–ø–∞–¥–∞—é—â–µ–µ –º–µ–Ω—é */
  menuIsOpen?: boolean;
  /** –ö–æ–ª–ª–±–µ–∫ –Ω–∞ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è input */
  onInputChange?: (event: ChangeEvent<HTMLInputElement>, value: string) => void;
  /** –ö–æ–ª–ª–±–µ–∫ –¥–ª—è –∫–∞—Å—Ç–æ–º–∏–∑–∞—Ü–∏–∏ –æ–ø—Ü–∏–π */
  renderElementOption?: (options: string | IOptionSuggest<any>) => any;
  /** –ö–æ–ª–ª–±–µ–∫ –¥–ª—è –≤—ã–±–æ—Ä–∞ –æ–ø—Ü–∏–π dropDown */
  onChange: (value: string | IOptionSuggest<any>) => void;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ dropdown */
  selectedItem?: string | IOptionSuggest<any>;
  /** –ó–Ω–∞—á–µ–Ω–∏–µ –∏–Ω–ø—É—Ç–∞ */
  value?: string;
}

export interface IOptionSuggest<T = any> {
  id: string | number;
  label: string;
  value?: T;
}

export const InputSuggest: FC<IInputSuggest> = ({
  options = [],
  size = 'big',
  className,
  load = false,
  dataTestId,
  noopMessage = DEFAULT_NOOP_MESSAGE,
  charsToShowSuggest = CHARS_TO_SHOW_SUGGEST,
  disabled,
  loadMessage = DEFAULT_LOAD_MESSAGE,
  tooltipContainer,
  menuMaxHeight = DEFAULT_MENU_HEIGHT,
  filterOptions,
  additionalText,
  onChange,
  menuIsOpen,
  renderElementOption,
  onInputChange,
  value,
  selectedItem,
  ...otherProps
}) => {
  const [showMenu, setShowMenu] = useState(false);
  const [inputValue, setInputValue] = useState(getInputValue(value, selectedItem));

  const inputRef: RefObject<HTMLInputElement> = useRef(null);

  const shouldRenderDropdown =
    menuIsOpen === undefined
      ? inputValue.length >= charsToShowSuggest && showMenu && !disabled
      : inputValue.length >= charsToShowSuggest && menuIsOpen && !disabled;

  return (
    <Wrapper className={className} data-test-id={dataTestId}>
      <Input
        value={inputValue}
        size={size}
        inputRef={inputRef}
        setShowMenu={menuIsOpen === undefined ? setShowMenu : undefined}
        disabled={disabled}
        additionalText={menuIsOpen || showMenu ? undefined : additionalText}
        onInputchange={onInputChange}
        setInputValue={setInputValue}
        onChangeSelected={onChange}
        {...otherProps}
      />

      <DropDownMenu
        load={load}
        options={filterOptions ? filterOptions(options) : defaultFilterOptions(options, inputValue)}
        size={size}
        setShowMenu={setShowMenu}
        showMenu={showMenu}
        menuIsOpen={menuIsOpen}
        noopMessage={noopMessage}
        inputRef={inputRef}
        tooltipContainer={tooltipContainer}
        loadMessage={loadMessage}
        menuMaxHeight={menuMaxHeight}
        renderElementOption={renderElementOption}
        onChange={onChange}
        setInputValue={setInputValue}
        inputValue={inputValue}
        selectedItem={selectedItem}
        shouldRenderDropdown={shouldRenderDropdown}
      />
    </Wrapper>
  );
};
